.consumer-signup{
  width: 100vw;
}
.signup-container {
  max-width: 400px;
  margin: 50px auto;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 8px;
  text-align: center;
}

h2 {
  margin-bottom: 20px;
}

form {
  display: flex;
  flex-direction: column;
}

.input-group {
  display: flex;
  align-items: center;
  margin-bottom: 15px;
}

.input-icon {
  margin-right: 10px;
}

input {
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  flex: 1;
}

button {
  padding: 10px;
  background-color: #3498db;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.login-link {
  margin-top: 20px;
}

a {
  color: #3498db;
  text-decoration: none;
}

/* Responsive CSS */

@media (max-width: 500px) {
  .signup-container {
    width: 80%;
  }
}
